<template>
    <div class="home">
        <md-content class="main-content" :style="{height: activeSong.name === '' ? '100%' : 'calc(100% - 250px)'}">
            <h1 class="title md-title">Liked Songs</h1>
            <md-button class="md-raised md-primary shuffle-button" @click="shufflePlay">Shuffle Play</md-button>
            <song-list class="song-list" :active-song="activeSong" :songs="songs" @play="playSong"></song-list>
        </md-content>
        <md-content class="bottom-card md-primary" v-if="activeSong.name !== ''">
            <div class="song-card">
                <div class="album-art" :style="{'background-image': `url(${activeSong.album.images[0].url})`}"></div>
                <div class="song-info">
                    <span>{{activeSong.name}}</span>
                    <span>{{activeSong.artists.map(a=>a.name).join(', ')}}</span>
                </div>
            </div>
            <div class="seek-bar" @mousedown="seekDown" @touchstart="seekDown($event.touches[0])">
                <span class="song-time">{{secondsToHms(songProgress * activeSong.duration_ms/1000)}}</span>
                <div class="seek-container" ref="seekContainer">
                    <div class="seek-progress" :style="{'width':`${Math.round(songProgress * 100)}%`}"></div>
                    <div class="seek-thumb"
                         :style="{'left':`calc(${Math.round(songProgress * 100)}% - 16px / 2)`}"></div>
                </div>
                <span class="song-duration">{{secondsToHms(activeSong.duration_ms/1000)}}</span>
            </div>
            <div class="music-controls">
                <md-button class="md-icon-button" @click="toggleShuffle()">
                    <md-icon :style="shuffle ? 'opacity: 1' : 'opacity: 0.6'">shuffle</md-icon>
                </md-button>
                <md-button @click="skipPrevious" class="md-icon-button">
                    <md-icon>skip_previous</md-icon>
                </md-button>
                <md-button @click="playing ? pause() : play()" class="md-icon-button md-raised">
                    <md-icon v-if="playing">pause</md-icon>
                    <md-icon v-else>play_arrow</md-icon>
                </md-button>
                <md-button @click="skipNext" class="md-icon-button">
                    <md-icon>skip_next</md-icon>
                </md-button>
                <md-button class="md-icon-button" @click="repeat = !repeat">
                    <md-icon :style="repeat ? 'opacity: 1' : 'opacity: 0.6'">repeat</md-icon>
                </md-button>
            </div>
        </md-content>
        <audio ref="player" class="audio-player"></audio>
    </div>
</template>

<script>

    import SongList from "../components/SongList";
    import StreamApi from "../js/StreamApi";

    const isLocal = location.href.includes('localhost') || location.href.includes('127.0.0.1');
    // const isLocal = false;
    const server = isLocal ? 'http://localhost:3000' : 'https://ruurd.dev:3000';
    StreamApi.setServer(server);

    export default {
        data() {
            return {
                timeUpdater: -1,
                songProgress: 0,
                seeking: false,
                songs: [],
                shuffledSongs: [],
                playing: false,
                repeat: true,
                shuffle: false,
                activeSong: {name: '', artists: [], album: {images: [{url: ''}]}}
            }
        },
        async mounted() {
            this.getToken();
            let token = JSON.parse(localStorage.token);

            let tracks;
            if (localStorage.getItem('tracks')) {
                console.log("Retrieving tracks from Cache");
                tracks = JSON.parse(localStorage.tracks);
                // Retrieved tracks from cache, update anyways:
                // Not when in dev though :Hmm
                console.log("Updating tracks from Cache");
                this.getTracks(token).then(t => {
                    this.songs = t.map(t => t.track);
                    localStorage.tracks = JSON.stringify(t);
                });
            } else {
                console.log("Retrieving tracks from Spotify");
                tracks = await this.getTracks(token);
            }
            this.songs = tracks.map(t => t.track);
            localStorage.tracks = JSON.stringify(tracks);
            // console.log("Your tracks are ", this.songs.map(t => t.popularity).reduce((a, b) => a + b) / this.songs.length, '% normie');
            console.log(this.songs);
            if (localStorage.getItem('lastPlayed') !== null) {
                let lastPlayed = this.songs.find(s => s.id === localStorage.lastPlayed);
                if (lastPlayed)
                    this.playSong(lastPlayed, true)
            }

            document.addEventListener('mousemove', e => this.mouseMove(e));
            document.addEventListener('touchmove', e => this.mouseMove(e.touches[0]));
            document.addEventListener('mouseup', e => this.mouseUp(e));
            document.addEventListener('touchend', e => this.mouseUp(e.touches[0]));

            this.timeUpdater = setInterval(() => {
                let progress = this.$refs.player.currentTime / (this.activeSong.duration_ms / 1000);
                progress = Math.max(Math.min(progress, 1), 0);
                this.songProgress = progress;
            }, 200);
        },
        beforeDestroy() {
            clearInterval(this.timeUpdater);
        },
        methods: {
            shufflePlay() {
                this.toggleShuffle(true);
                this.playSong(this.shuffledSongs[0]);
            },
            shuffleArray(array) {
                let m = array.length, t, i;
                // While there remain elements to shuffle…
                while (m) {
                    // Pick a remaining element…
                    i = Math.floor(Math.random() * m--);
                    // And swap it with the current element.
                    t = array[m];
                    array[m] = array[i];
                    array[i] = t;
                }

                return array;
            },
            toggleShuffle(forceValue) {
                if (forceValue !== undefined)
                    this.shuffle = forceValue;
                else
                    this.shuffle = !this.shuffle;

                if (this.shuffle)
                    this.shuffledSongs = this.shuffleArray([...this.songs]);
            },
            secondsToHms(seconds) {
                if (isNaN(seconds) || seconds === undefined)
                    return '0:00';

                seconds = Math.round(seconds);
                let h = Math.floor(seconds / 3600);
                let m = Math.floor((seconds % 3600) / 60);
                let s = seconds % 60;
                h = h.toString();
                m = m.toString();
                s = s.toString();
                if (h !== '0') {
                    m = m.padStart(2, '0');
                    s = s.padStart(2, '0');
                }
                s = s.padStart(2, '0');

                if (h === '0')
                    return `${m}:${s}`;
                else return `${h}:${m}:${s}`;
            },
            skipNext() {
                let currentIndex = this.songs.indexOf(this.activeSong);
                if (currentIndex === this.songs.length - 1)
                    if (this.repeat)
                        currentIndex = -1;
                    else {
                        console.log("Not skipping, repeat is off");
                        return;
                    }
                if (this.shuffle)
                    this.playSong(this.shuffledSongs[currentIndex + 1]);
                else
                    this.playSong(this.songs[currentIndex + 1]);

            },
            skipPrevious() {
                let player = this.$refs.player;
                if (player.currentTime < 5) {
                    let currentIndex = this.songs.indexOf(this.activeSong);
                    if (currentIndex === 0)
                        currentIndex = this.songs.length;
                    if (this.shuffle)
                        this.playSong(this.shuffledSongs[currentIndex - 1]);
                    else
                        this.playSong(this.songs[currentIndex - 1]);
                } else player.currentTime = 0;
            },
            mouseUp() {
                if (this.seeking)
                    this.seeking = false;
            },
            mouseMove(e) {
                if (this.seeking)
                    this.seekToEvent(e);
            },
            seekDown(e) {
                this.seeking = true;
                this.seekToEvent(e);
            },
            seekToEvent(e) {
                let percent = (e.pageX - this.$refs.seekContainer.offsetLeft) / this.$refs.seekContainer.offsetWidth;
                percent = Math.max(Math.min(percent, 1), 0);
                this.songProgress = percent;
                let duration = this.activeSong.duration_ms / 1000;
                this.$refs.player.currentTime = duration * percent;
            },
            async play() {
                console.log('play');
                if (this.activeSong.name === '') return;
                this.playing = true;
                await this.$refs.player.play();
            },
            pause() {
                console.log('pause');
                this.playing = false;
                this.$refs.player.pause();
            },
            setSongMetaData: function (song) {
                let artists = song.artists.map(a => a.name).join(', ');
                document.title = artists + ' - ' + song.name;
                // document.querySelector('meta[name="theme-color"').content = song.color;

                if (!('mediaSession' in navigator))
                    return;

                navigator.mediaSession.metadata = new MediaMetadata({
                    title: song.name,
                    artist: artists,
                    artwork: [
                        {src: song.album.images[0].url, type: 'image/png'},
                    ]
                });
                navigator.mediaSession.setActionHandler('play', async () => {
                    await this.play();
                });
                navigator.mediaSession.setActionHandler('pause', async () => {
                    await this.pause();
                });
                navigator.mediaSession.setActionHandler('previoustrack', () => {
                    this.skipNext();
                });
                navigator.mediaSession.setActionHandler('nexttrack', () => {
                    this.skipPrevious();
                });
            },
            async playSong(song, pause = false) {
                if (song.id === this.activeSong.id) {
                    this.$refs.player.currentTime = 0;
                    if (!pause)
                        this.play();
                    return;
                }
                localStorage.lastPlayed = song.id;
                this.activeSong = song;
                this.setSongMetaData(song);
                //TODO: if song is stored locally play that
                let query = song.artists.map(n => n.name).join(' ') + ' - ' + song.name;
                let results = await StreamApi.search(query);
                let url = StreamApi.getStreamUrl(results[0].id);
                let player = this.$refs.player;
                player.src = url;
                player.oncanplay = async () => {
                    this.playing = !pause;
                    if (!pause)
                        await player.play();
                    // else
                    //     player.pause();
                };
                player.onended = () => this.skipNext();
            },
            async getTracks(token) {
                //Get saved tracks
                let tracks = [];
                let url = 'https://api.spotify.com/v1/me/tracks?limit=50';
                while (url) {
                    let newTracks = await (await fetch(url, {
                        method: 'GET',
                        headers: {
                            'Authorization': 'Bearer ' + token.token,
                        }
                    })).json();
                    url = newTracks.next;
                    tracks.push(newTracks);
                }
                let combinedTracks = tracks.map(t => t.items).reduce((a, b) => a.concat(b));

                return combinedTracks;
            },
            async getToken() {
                if (localStorage.getItem('token') !== null) {
                    let token = JSON.parse(localStorage.token);
                    token.expires = +token.expires;
                    let unixSeconds = Math.floor((+new Date()) / 1000);
                    // if token is 10 minutes or older, request new one
                    if (token.expires - unixSeconds >= 3000) {
                        console.log('didnt request new token :)');
                        return token;
                    }
                }
                const clientId = 'cd272aa2194c46c7a460e9a202f66002';
                let redirectUrl = isLocal ? 'http://localhost:8080/vuemusic/#/' : 'https://ruurd.dev/vuemusic/#/';

                //Check for login error
                if (location.search.includes('error')) {
                    document.write("Error, access was not granted");
                    return;
                }

                if (localStorage.getItem('state') === null)
                    localStorage.state = Math.random();

                //Check for auth code
                if (!location.hash.includes("access_token=")) {
                    let scopes = 'user-library-read';
                    location.href = `https://accounts.spotify.com/authorize?response_type=token&state=${localStorage.state}&client_id=${clientId}${scopes ? '&scope=' + encodeURIComponent(scopes) : ''}&redirect_uri=${encodeURIComponent(redirectUrl)}`;
                    return;
                }

                // let [token, type, expire] = location.hash.split('=').map(i => i.split('&')[0]).slice(1);
                let token = location.hash.split('access_token=')[1].split('&')[0];
                let tokenType = location.hash.split('token_type=')[1].split('&')[0];
                let tokenExpires = location.hash.split('expires_in=')[1].split('&')[0];
                let tokenState = location.hash.split('state=')[1].split('&')[0];

                //Remove for vue history mode
                this.$router.push('/');

                if (tokenState !== localStorage.state) {
                    document.write("Error, possible mitm attack, state doesn't match");
                    localStorage.state = Math.random();
                    return;
                }

                localStorage.state = Math.random();
                localStorage.token = JSON.stringify({
                    token,
                    type: tokenType,
                    expires: Math.floor((+new Date) / 1000) + (+tokenExpires)
                })
            },
        },
        name: 'home',
        components: {SongList}
    }

</script>

