        text-align: center;
    }

    .main-content {
        height: calc(100% - 250px);
        position: absolute;
        width: 100%;
        padding: 90px 10px 20px;
        overflow-y: auto;
        background: radial-gradient(circle at top left, rgba(75, 47, 250, 0.9) 0%, transparent 65%), radial-gradient(circle at top right, rgba(235, 255, 240, .6) 0%, transparent 35%);
    }

    .title {
        margin: 0 0 40px;
        font-size: 24px;
        font-weight: 600;
    }

    .shuffle-button {
        border-radius: 25px;
        width: 230px !important;
