        font-size: 16px;
    }

    .shuffle-button .md-ripple {
        border-radius: 25px;
    }

    .song-list {
        margin-top: 20px;
    }

    .bottom-card {
        position: absolute;
        height: 250px;
        bottom: 0;
        width: 100%;
        z-index: 5;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.3);
        padding: 20px;
    }

    .song-card {
        display: flex;
        flex-direction: row;
        margin: 15px;
    }

    .album-art {
        width: 55px;
        height: 55px;
        border-radius: 3px;
        box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.3);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        padding: 10px;
    }

    .song-info {
        display: flex;
        /*text-shadow: 0px 0px 20px black;*/
        flex-direction: column;
        text-align: left;
        padding: 10px 15px;
        max-width: calc(100% - 55px);
    }

    .song-info > span:first-child {
        font-weight: 500;
