        border-radius: 50%;
        position: relative;
        top: calc(-16px / 2 - 2.5px);
        left: calc(50% - 16px / 2);
    }

    .music-controls {
        display: flex;
        justify-content: space-evenly;
    }

    .control-play {

    }
</style>
