import CustomApi from './CustomApi';

class StreamApi extends CustomApi {
    constructor() {
        super('');
    }

    setServer(server) {
        this.baseUrl = server;
    }

    async search(query) {
        return await this.get('search?query=' + encodeURIComponent(query));
    }

    getStreamUrl(ytId) {
        return this.baseUrl + '/stream/?id=' + ytId;
    }
}

export default new StreamApi();