<template>
    <div class="song-list">
        <md-list-item class="song-item" v-for="song in songs" @click="playSong(song)">
            <div class="md-list-item-text">
                <span class="song-title">{{song.name}}</span>
                <span class="song-artist">{{song.artists.map(a=>a.name).join(', ')}}</span>
            </div>
            <md-button v-if="activeSong.id === song.id" class="md-primary md-icon-button md-list-action md-disabled">
                <md-icon class="song-options">volume_up</md-icon>
            </md-button>
            <md-button class="md-icon-button md-list-action">
                <md-icon class="song-options">more_vert</md-icon>
            </md-button>
        </md-list-item>
    </div>
</template>

<script>
    export default {
        name: "SongList",
        props: {
            songs: Array,
            activeSong: Object,
        },
        data() {
            return {}
        },
        methods: {
            playSong(song) {
                this.$emit('play', song);
            }
        },
        mounted() {

        }
    }
</script>

<style scoped>
    .song-list {
        background-color: transparent !important;
    }

    .song-list li {
        list-style: none !important;
    }

    .song-item {
        margin-bottom: 10px;
    }

    .song-title, .song-artist {
        font-weight: 300;
    }

    .song-artist, .song-options {
        opacity: 0.6;
    }

    .song-artist {
        margin-top: 4px;
        font-size: 13px !important;
    }
</style>